const getBadge = status => {
    switch (status) {
        case 'Active': return 'success'
        case 'Inactive': return 'secondary'
        case 'Pending': return 'warning'
        case 'Banned': return 'danger'
        default: return 'primary'
    }
}
const fields = [ 'Action','S No','Sujet de message ', 'To', 'To(Roles)', 'Cc', 'Bcc', ]

const MessageList = (props) => {
    const [currentItems, setCurrentItems] = React.useState([]);
    const [notifications, setNotifications] = React.useState([]);
    const [domainId, setDomainId] = React.useState(JSON.parse(localStorage.getItem('domain_data')).domainId);
    const show = useSelector(state => state.sidebarShow)
    const dt = useRef(null);
    const [globalFilter, setGlobalFilter] = React.useState("");
    const [domainName, setDomainName] = React.useState(JSON.parse(localStorage.getItem('domain_data')).domainName);
    const [permissions, setPermissions] = React.useState([]);
    const [visible, setVisible] = useState(true)

    const menu = useRef(null);
    useEffect(()=> {
        const role_permissions = JSON.parse(localStorage.getItem("userPermissions"))
        const permissionTab = []
        if(role_permissions.length ){
            role_permissions.map(rolePermissions =>{
                rolePermissions.map(permission =>{
                    permissionTab.push(permission.key)
                })
            })
            setPermissions(permissionTab)

        }
        const messaging_data = {
            method: 'GET',
            headers: {
                "X-Tenant": domainName,
                'Content-Type': 'application/json',
                "Authorization": "Bearer " + localStorage.getItem("Authorization")}

        };
        fetch(MESSAGING_SERVICE + '/api/auth/getAll', messaging_data)
            .then(response => {
                return new Promise((resolve, reject) => {
                    response.text().then(body => {
                        resolve(
                            body.length ? JSON.parse(body) : null
                        );
                    }).catch(err => {
                        reject(err);
                    });
                });
            })
            .then(data => {
                console.log(data)
                var notif_data = [];
                data.map(messaging =>
                {
                    notif_data.push({"S No":messaging.sno, "Sujet": messaging.messagingSubject ,
                        "To": messaging.messagingTo, 'Cc': messaging.messagingCc, 'Bcc': messaging.messagingBcc ,
                        "id":messaging.id})
                })

                setNotifications(notif_data)

                setCurrentItems(notifications)
                if(notif_data.size !== 0){
                    setVisible(false)
                }
                //this.props.history.push("/admin/formView");
            })
            .catch(error => {
                console.log(error);
            });
    },[])

    const menuList = (rowData) => {
        const items =
            [  {
                label: 'Aperçu',
                icon: 'pi pi-eye',
                command:()=>{ window.location.hash="/admin/notification/preview/" + rowData.id;}
            },
                {
                    label: 'Modifier',
                    icon: 'pi pi-pencil',
                    command:()=>{ window.location.hash="/admin/notification/edit/" + rowData.id ;}
                },
                {
                    label: 'Supprimer',
                    icon: 'pi pi-trash',
                    //   command:()=>{ window.location.hash="/admin/notification/edit/" + rowData.id;
                }
            ]
        return (  <div>
            <Menu model={items} popup ref={menu} id="popup_menu" />
            <i className="pi pi-bars" onClick={(event) => menu.current.toggle(event)}></i>
        </div>)
        //  return <i className="pi pi-bars p-toolbar-separator p-mr-2" onClick={(event) => <Menu model={items} popup ref={menu} />}></i> ;
    }
    const csvContent = currentItems.map(item => Object.values(item).join(',')).join('\n')
    const csvCode = 'data:text/csv;charset=utf-8,SEP=,%0A' + encodeURIComponent(csvContent)


    const menuPermissions = (item)=>{



        if (permissions.includes("0-0-14-1")){
            if (permissions.includes("0-0-14-2")){
                return  (
                    <CDropdownMenu className="mp-0">
                        <CDropdownItem to={"/admin/messaging/preview/" + item.id}>
                            <RemoveRedEye/>&nbsp;&nbsp;Aperçu
                        </CDropdownItem>
                       {/* <CDropdownItem to={"/admin/messaging/edit/" + item.id}>
                            <Edit/> &nbsp;&nbsp;Editer
                        </CDropdownItem>*/}
                        {/*   <CDropdownItem>
                            <Delete/>&nbsp;&nbsp;Supprimer
                        </CDropdownItem>*/}
                    </CDropdownMenu>)
            }else{
                return  (
                    <CDropdownMenu className="mp-0">
                        <CDropdownItem to={"/admin/messaging/preview/" + item.id}>
                            <RemoveRedEye/>&nbsp;&nbsp;Aperçu
                        </CDropdownItem>
                        {/*<CDropdownItem to={"/admin/notification/edit/" + item.id}>
                            <Edit/> &nbsp;&nbsp;Editer
                        </CDropdownItem>*/}

                    </CDropdownMenu>
                )
            }

        }else{
            if (permissions.includes("0-0-14-2")){
                return  (
                    <CDropdownMenu className="mp-0">
                        <CDropdownItem to={"/admin/messaging/preview/" + item.id}>
                            <RemoveRedEye/>&nbsp;&nbsp;Aperçu
                        </CDropdownItem>

                        {/* <CDropdownItem>
                            <Delete/>&nbsp;&nbsp;Supprimer
                        </CDropdownItem>*/}
                    </CDropdownMenu>)
            }else{
                return  (
                    <CDropdownMenu className="mp-0">
                        <CDropdownItem to={"/admin/messaging/preview/" + item.id}>
                            <RemoveRedEye/>&nbsp;&nbsp;Aperçu
                        </CDropdownItem>

                    </CDropdownMenu>
                )
            }
        }

    }
    const createNotification =() =>{
        if (permissions.includes("0-0-14-0")){
            return ( <CLink
                style={{color: "#FFFFFF"}}
                onClick={()=> props.history.push("/admin/messaging/send")}
                rel="noreferrer noopener"
                target="_blank"
                className="card-header-action"
            >
                <Add/>Ajouter un nouveau
            </CLink>)
        }
    }
    const reset = () => {
        setGlobalFilter('');
        dt.current.reset();
    }
    const header = (
        <div className="table-header">
            <Button type="button" label="Clear" className="p-button-outlined" icon="pi pi-filter-slash" onClick={reset} />
            <span className="p-input-icon-left">
                <i className="pi pi-search" />
                <InputText type="search" value={globalFilter} onChange={(e) => setGlobalFilter(e.target.value)} placeholder="Global Search" />
            </span>
        </div>
    );
    const isVisible = visible;

    return (
        <>

            <CRow>
                <CCol>
                    <CCard style={{zIndex: "10 !important"}}>
                        <CCardHeader style={{backgroundColor: "#494399"}}>
                            <span style={{color: "#FFFFFF", fontWeight: "bold"}}>Messagerie</span>


                            <div className="card-header-actions">
                                {createNotification()}
                                {/* <CLink style={{color: "#FFFFFF", paddingLeft: "10px" }} >
              <Search content="cil-search" size="2xl"/>
              </CLink>*/}

                                <CLink style={{color: "#FFFFFF", paddingLeft: "10px" }} >
                                    {/* <Menu content="cil-search" size="2xl"/>*/}
                                    <i  size="2xl" className="pi pi-bars" onClick={(event) => console.log(event)}></i>
                                </CLink>
                            </div>
                        </CCardHeader>
                        <CCardBody>
                            <div className="card">
                                {isVisible && <ProgressBar  mode="indeterminate" style={{ height: '6px' }}></ProgressBar>}

                                <DataTable value={notifications} ref={dt} header={header}  globalFilter={globalFilter} scrollable dataKey="id" paginator rows={10} rowsPerPageOptions={[5, 10, 25]}
                                           paginatorTemplate="FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink CurrentPageReport RowsPerPageDropdown"
                                           currentPageReportTemplate="{currentPage} of {totalPages} pages ({totalRecords} items)"  className="p-datatable-sm p-datatable-striped p-datatable-gridlines">
                                    <Column field={ item => {return (<td>
                                        <CDropdown className="m-1">
                                            <CDropdownToggle style={{padding:'0px'}}>
                                                <i  size="2xl" className="pi pi-bars"></i>
                                            </CDropdownToggle>
                                            {menuPermissions(item)}
                                        </CDropdown>
                                    </td>)}} style={{width:"50px"}}></Column>
                                    <Column field='S No' header="S No"  sortable ></Column>
                                    <Column field="Sujet" header="Sujet"  sortable></Column>
                                    <Column field="To" header="A"  sortable ></Column>
                                    <Column field="Cc" header="Cc"  sortable ></Column>
                                    <Column field="Bcc" header="Bcc"  sortable ></Column>


                                </DataTable>
                            </div>

                        </CCardBody>

                    </CCard>
                </CCol>
            </CRow>
        </>
    )
}
export default MessageList
